"use client";

import React from "react";
import { usePathname } from "next/navigation";
import Link from "next/link";

import "./globals.css";

import { Inter } from "next/font/google";

const inter = Inter({
  subsets: ["latin"],
  variable: "--font-inter",
  display: "swap",
});

const navLinks = [
  { href: "/", label: "Strona główna" },
  { href: "/about", label: "O nas" },
  { href: "/services", label: "Usługi" },
  { href: "/contact", label: "Kontakt" },
];

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();

  return (
    <html lang="pl" className={`${inter.variable} font-sans bg-white text-black`}>
      <head>
        <title>Zajebista Strona</title>
        <meta name="description" content="Nowoczesna czarno-biała strona z animacjami" />
      </head>
      <body className="min-h-screen flex flex-col">
        <header className="sticky top-0 bg-white border-b border-gray-300 z-50">
          <nav className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
            <div className="text-2xl font-bold tracking-wide">Zajebista Strona</div>
            <ul className="flex space-x-6">
              {navLinks.map(({ href, label }) => (
                <li key={href}>
                  <Link
                    href={href}
                    className={`text-lg font-medium hover:text-gray-600 transition-colors ${
                      pathname === href ? "underline decoration-2 underline-offset-4" : ""
                    }`}
                  >
                    {label}
                  </Link>
                </li>
              ))}
            </ul>
          </nav>
        </header>
        <main className="flex-grow max-w-7xl mx-auto px-6 py-8">{children}</main>
        <footer className="bg-white border-t border-gray-300 py-6 text-center text-sm text-gray-600">
          &copy; 2024 Zajebista Strona. Wszelkie prawa zastrzeżone.
        </footer>
      </body>
    </html>
  );
}
